@charset "UTF-8";

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;400;500;800;900&family=Noto+Sans+Khmer:wght@100;200;300;400;500;600;700;800;900&family=Noto+Sans:wght@400;700&display=swap');

:root {
	$primary: #000000;
	$secondary: white;
	$danger: red;
	--ion-default-font: "Inter", Noto Sans Khmer, sans-serif;

	/** primary **/
	--ion-color-primary: #0072C6 !important;
	--ion-color-primary-rgb: 45, 86, 234;
	--ion-color-primary-contrast: #FFFFFF;
	--ion-color-primary-contrast-rgb: 255, 255, 255;
	--ion-color-primary-shade: #3171E0;
	--ion-color-primary-tint: #4C8DFF;

	//color
	--color: #000000;
	--normal: #000000;
	--primary: black;
	--secondary: white;
	--danger: red;
	//padding
	--pdsmall:6px;
	--pdLarge:8px;
	--pdLarger:12px;

}

/***************************************************************************************************
	Reset
***************************************************************************************************/
:root {
	--ion-color-primary: #0067AC;
	--color:#000000;
}
* { box-sizing: border-box;}
::-webkit-scrollbar { width: 8px; height: 8px; background-color: #FFFFFF; margin-right: 3px; border-radius: 2px; -webkit-border-radius: 2px; -moz-border-radius: 2px; -ms-border-radius: 2px; -o-border-radius: 2px; }
::-webkit-scrollbar-track { box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.2); }
::-webkit-scrollbar-thumb { background-color: #4C95DD;}
html, body { height:100%; min-height:450px; margin:0; padding:0; -webkit-text-size-adjust:none;}
html { font:normal 16px "Inter", Noto Sans Khmer, sans-serif; --ion-default-font:"Inter", Noto Sans Khmer, sans-serif !important;}
body { min-height:100%; color:#231f20; background:transparent; word-wrap:normal; word-break:keep-all;}
img { border:none;}
p, ul, li, ol, h1, h2, h3, h4, h5, h6, dl, dt, dd, em, figure { margin:0; padding:0;}
h1, h2, h3, h4, h5, h6 { font-weight:900; font-size:12px;}
li { list-style:none;}
fieldset { margin:0; padding:0; border:none;}
legend { position:absolute; left:-1000px; top:-1000px; height:0; font-size:0; line-height:0; visibility:hidden;}
a { margin:0; color:#231f20; text-decoration:none; outline:none; cursor:pointer;}
table { width:100%; border-collapse:collapse; font-size:12px;}
table caption { display:none;position:absolute; left:-999px; height:0; font-size:0; line-height:0; visibility:hidden; overflow:hidden;}
table td, table th { padding:0;}
button { margin:0; padding:0; font:normal 1em "Inter", Noto Sans Khmer, sans-serif; color:#333333; vertical-align:middle; background:0; border:none; overflow:visible; cursor:pointer; box-shadow: none;}
button:focus { outline:none; -webkit-tap-highlight-color:rgba(0,0,0,0.2);}
input[type=text], input[type=password], input[type=number], input[type=tel], input[type=rang], input[type=email], input[type=url], input[type=date], input[type=datetime], input[type=month], input[type=tel], input[type=search], input[type=color], input[type=file]
{ display:inline-block; height:48px; margin:0; padding: 0 20px !important; font-weight:normal; font-size:16px; font-family: 'Inter', sans-serif; color:#231f20; vertical-align:middle; background:none; border:none; border-bottom:2px solid rgba(35,31,32,.1); border-radius:24px; -webkit-box-sizing:border-box; -moz-box-sizing:border-box; box-sizing:border-box; -webkit-appearance:none; -moz-appearance:none; -o-appearance:none; appearance:none; -webkit-border-radius:24px; -moz-border-radius:24px; -ms-border-radius:24px; -o-border-radius:24px;
}
input[type=checkbox], input[type=radio] { margin:0; vertical-align:middle; font-size:12px; border:none; border-radius:0; -webkit-appearance:none; cursor:pointer;}
input[type=radio] { position:relative; width:24px; height:24px; margin-right:6px; vertical-align:middle; background:url("../images/ico_rdobox.svg") no-repeat left center; background-size:24px auto;}
input[type=checkbox]:only-child, input[type=radio]:only-child { position:static; margin:0; opacity:1;}
input[type=radio]:checked, ion-radio.radio-checked { background: #0067AC url("../images/ico_rdobox_check.svg") center no-repeat;}
input[type=file] { -webkit-box-sizing:border-box; -moz-box-sizing:border-box; box-sizing:border-box;}
input[type=text]:only-child, input[type=password]:only-child, input[type=search]:only-child, input[type=number]:only-child, input[type=email]:only-child, input[type=tel]:only-child, .k-textbox:only-child { max-width:100%; width:100%;}
input::-ms-input-placeholder, input::-moz-placeholder { font-weight:normal; color:#231f20 !important; opacity:.3;}
input:read-only { background-color: #F7F7F7 !important;}
input::placeholder { font-weight:normal; color:#231f20 !important; opacity:.3; text-align: left !important;}
input::-webkit-input-placeholder { font-weight:normal; color:#231f20 !important; opacity:.3; text-align: left !important;}
input::-ms-clear,input::-ms-reveal { display: none;}

input:focus, textarea:focus, select:focus { outline:none; -webkit-tap-highlight-color:rgba(0,0,0,0.2);}
input:focus:not([type=checkbox]):not([type=radio]), textarea:focus, .k-textbox:focus { border-color:#0067AC;}
input[readonly=readonly]:not([type=checkbox]):not([type=radio]) { color:#231f20; background:#d5d2ce;}
input[readonly=readonly]:focus:not([type=checkbox]):not([type=radio]) { padding:5px 12px; border:none;}
input:disabled { color:#CDCDCD; background:#F1F1F1;}
input.num { text-align:right;}
select { background-color: #FFFFFF; margin:0; min-width:64px; max-width:100%; height:48px; padding:0 24px 3px 20px; font-style:normal; font-size:16px; font-family: 'Inter', sans-serif; color:#333333; vertical-align:middle; text-overflow:ellipsis; background: #FFFFFF url("../images/ico_dropdown.svg") no-repeat right 20px center; background-size:18px auto; border:none; border-radius:50px; cursor:pointer; -webkit-appearance:none; -moz-appearance:none; -ms-appearance:none; appearance:none; white-space:nowrap; overflow:hidden; -webkit-box-sizing:border-box; -moz-box-sizing:border-box; box-sizing:border-box; -webkit-border-radius:50px; -moz-border-radius:50px; -ms-border-radius:50px; -o-border-radius:50px;
	&.line { border: 1px solid #DDDDDD; border-radius: 8px; -webkit-border-radius: 8px; -moz-border-radius: 8px; -ms-border-radius: 8px; -o-border-radius: 8px; }
}
select.no_border { border:none;}
select::-ms-expand { display:none;}
select:disabled { color:#CDCDCD; background:#F1F1F1;}
select:only-child { display:block; width:100%;}
label { position:relative;}
label, label > span { vertical-align:middle;}
textarea { width:100%; padding:9px 12px; font:normal 16px "Inter", Noto Sans Khmer, sans-serif; font-weight: 300; color:#3333; background:#eff2f5; border:none; border-radius:4px; -webkit-box-sizing:border-box; -moz-box-sizing:border-box; box-sizing:border-box;}
textarea:only-child { vertical-align:top;}
textarea::-webkit-input-placeholder, textarea::-webkit-input-placeholder, textarea::-ms-input-placeholder { color:#898989;}
textarea[readonly=readonly] { color:#434343;}
textarea:focus { padding:7px 10px;}
hr { display:block; height:1px; margin:24px 0; background:#ECF0F3; border:none;}
div, a, li { -webkit-box-sizing:border-box; -moz-box-sizing:border-box; -o-box-sizing:border-box; -ms-box-sizing:border-box; box-sizing:border-box;}
article, aside, footer, header, hgroup, nav, section, figure { display:block; padding:0; margin:0; box-sizing:border-box; -webkit-box-sizing:border-box;}

em, address { font-style:normal;}
em { color:#888888;}
sub { font-weight:normal; vertical-align:middle;}
img { max-width:100%; max-height:100%; vertical-align:top;}
input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button { -webkit-appearance:none; margin:0;}
del { color:#898989;}
iframe { display:block;}

/***** Ionic component style *****/
html, body { --color:#231f20;}
input:not([type=checkbox]):not([type=radio]), ion-input, ion-textarea { --padding-start: 20px; --padding-end: 20px; overflow: hidden; height:48px; padding:0; border: none; /* background: #FFFFFF;  */border-radius: 8px; -webkit-border-radius: 8px; -moz-border-radius: 8px; -ms-border-radius: 8px; -o-border-radius: 8px; 		
	&.line { border: 1px solid #DDDDDD; border-radius: 8px;}
}
ion-textarea.line { border-radius: 8px; border: 1px solid #DDD; background: #FFFFFF; --placeholder-font-weight: 300; --placeholder-opacity: 1 !important; --placeholder-color: #333333; font-size: 16px; font-weight: 300;}
ion-input.sc-ion-input-md-h { --background: #FFFFFF; background-color: #FFFFFF;}
ion-input:disabled { --background: #DDDDDD;}
ion-input:disabled input { background-color: #F7F7F7 !important; color: #333333;}
input:not([type=checkbox]):not([type=radio]):focus, ion-input.has-focus { height:48px; padding:0; border-color:#0067AC;}
/* ion-input input:not([type=checkbox]):not([type=radio]) { border-bottom:none; height: 100%;  background-color: #FFFFFF;} */
ion-input input { height: 100%;}
ion-input.inp_amount { --padding-end: 50px !important; text-align: right;  font-size: 18px; font-weight: 700;}
ion-input.inp_amount input { padding-right: 50px !important;}
ion-input.inp_amount .unit { position: absolute; right: 0; margin-right: 20px; font-size: 12px; color: #888888; margin-top: 4px;}
ion-input.inp_rate { --padding-end: 35px !important; text-align: right;  font-size: 18px; font-weight: 700;}
ion-input.inp_rate input { padding-right: 35px !important;}
ion-input.inp_rate .unit { position: absolute; right: 0; margin-right: 20px; font-size: 12px; color: #888888; margin-top: 4px;}
ion-input .unit ~ .input-clear-icon, ion-input .contact ~ .input-clear-icon, ion-input .date ~ .input-clear-icon { margin-right:28px;}
.input-clear-icon.sc-ion-input-md, .input-clear-icon.sc-ion-input-ios { width: 16px; height: 16px; background:url("../images/ico_del_inp.svg") no-repeat center center; margin-right: 20px;}
ion-radio, ion-checkbox, ion-label { margin:0; vertical-align:middle;}
ion-radio { width:24px; height:24px; --color:transparent; --color-checked: transparent; --checkmark-color:transparent; --background:transparent; background: url("../images/ico_rdobox.svg") left center; background-size: 100% auto; margin:0 12px 0 0;  border-radius: 4px; overflow: hidden;}
ion-radio.radio-checked { background:  url("../images/ico_rdobox_check.svg") center no-repeat !important;}
ion-radio.radio02 { width:24px; height:24px; background-image:url("../images/ico_rdobox.svg");}
ion-radio.radio02.radio-checked { background: url("../images/ico_rdobox_check.svg");}
ion-checkbox { width:24px; height:24px; margin:0 5px 0 0; --checkmark-color:transparent; --background:transparent; background: url("../images/ico_chkbox.svg") no-repeat center; background-size: 24px auto; --border-width:0;}
ion-checkbox.checkbox-checked { --background-checked:transparent; background:#0067AC url("../images/ico_chkbox_check.svg") center no-repeat; border-radius: 4px; }
ion-checkbox:disabled { opacity: .3;}
.checkbox-icon path  { --checkmark-color:transparent;}
ion-item .list_cont { width:100%;}
.card-content-md { --padding-start:22px; --padding-end:22px; padding:14px 22px 13px; color:#231f20;}
ion-select { position:relative; height:48px; --padding-start: 20px; --padding-top:0; --padding-bottom:0; font-weight:500; --placeholder-opacity:#333333; font-size:16px; font-weight: 300; line-height:normal; background-color: #FFFFFF; overflow: hidden; border-radius: 8px; -webkit-border-radius: 8px; -moz-border-radius: 8px; -ms-border-radius: 8px; -o-border-radius: 8px;
	&.line { border: 1px solid #DDDDDD; border-radius: 8px;}
}
ion-select::part(icon) {opacity: 0;}
ion-select:before { content:""; position:absolute; right:0; top:50%; width:18px; height:18px; margin-top:-8px; background:#FFFFFF; border-radius:50%; z-index:1;}
ion-select:after { content:""; position:absolute; right:16px; top:50%; width:18px; height:18px; margin-top:-8px; background:url("../images/ico_dropdown.svg") right center no-repeat; background-size:18px auto; border-top: transparent !important; z-index:1;}
ion-select[aria-expanded=true] { color:#0067AC;}
ion-select:only-child { width:100%; max-width:100%;}
ion-select.select-disabled { opacity: 1; background-color: #f7f7f7; color: #000000;}
ion-select.select-disabled::before { opacity: 0;}
ion-item.item-select { --border-width:0 0 2px;}
ion-item.item-input { --border-width:0 0 1px !important; }
ion-item.item-lines-none { --border-width:0 !important;}
ion-item.item-select ion-label { color:rgba(35,31,32,.8);}
ion-item.item-select ion-label.label-stacked { font-size:13px; color:rgba(35,31,32,.8); transform:none;}
.alert-button-group.sc-ion-alert-md button { min-width:80px; height:36px; margin:0 3px; font-size:15px; color:#FFFFFF; text-align:center; background:#0067AC !important; border:none !important; border-radius:40px !important; box-shadow:none;}
.alert-button-group.sc-ion-alert-md button:first-child { --background:rgba(107,107,107,.8) !important; background:rgba(107,107,107,.8) !important;}
.alert-button-inner.sc-ion-alert-md { justify-content:center;}
ion-textarea, ion-textarea:only-child { --highlight-color-focused: #0067AC; height: 100px; margin:0;--padding-start: 20px; --padding-top:12px; --padding-bottom:7px; --placeholder-opacity:.3 !important; font-size: 17px; overflow: auto; }
ion-textarea textarea { height: 98px;}

ion-button { --box-shadow: none; margin: 0; --padding-start: 0; --padding-end: 0; --padding-top: 0; --padding-bottom: 0; overflow: hidden; text-transform: capitalize; font-weight: 300; letter-spacing: 0px;}

ion-alert { background:rgba(35,31,32,.6);}
ion-backdrop { background:none;}
.alert-wrapper.sc-ion-alert-md { border-radius:25px;}
.alert-title.sc-ion-alert-md { color:var(--color);}

.sc-ion-input-md-h { --background: #FFFFFF; background-color: #FFFFFF; }

.native-input[disabled].sc-ion-input-md { color: #000000; opacity: 1; background-color: #f7f7f7 !important; border-radius: 0; -webkit-border-radius: 0; -moz-border-radius: 0; -ms-border-radius: 0; -o-border-radius: 0; }

ion-row, ion-col { padding: 0;}
ion-item { --padding-start: 0; --padding-end: 0; --background: transparent; --background-hover: transparent; --border: none; --border-width: 0; --inner-border-width: 0; --inner-padding-end: 0; --inner-padding-start: 0;}

ion-content { --background: #FFFFFF; background: #FFFFFF; --padding-end: 0; --padding-top: 0; --padding-bottom: 0; min-width: 1080px;} 

.inp_box.type_datetime { 
	.inp { position: relative;
		.btn_date { position: absolute; right: 11px; top: 50%; margin-top: -17px; width: 34px; height: 34px; --background: transparent; background: url("../../assets/images/btn_ico_calendar.svg") center no-repeat; text-indent: -99999rem; z-index: 9999;
			&.button-disabled { --background: transparent !important; background: url("../../assets/images/btn_ico_calendar.svg") center no-repeat !important; border: none !important; filter: brightness(0) saturate(100%) invert(89%) sepia(0%) saturate(134%) hue-rotate(313deg) brightness(87%) contrast(87%); -webkit-filter: brightness(0) saturate(100%) invert(89%) sepia(0%) saturate(134%) hue-rotate(313deg) brightness(87%) contrast(87%); }
		}
	}
	ion-segment-button { width: 145px; }
}

.type_datetime input:read-only { background-color: #FFF !important;}

.inp_box .inp .unit { position: absolute; right: 10px; top: 0; font-size: 16px; font-weight: 300; z-index: 9; line-height: 48px; }

ion-modal { --width: 100%; --height: 100%; --background: transparent; --box-shadow: none;
	ion-page { --height: auto;}
	ion-content { --height: auto; height: auto; min-width: auto;}
}

ion-modal::part(backdrop) { opacity: 0.6 !important;}

.popup { display: flex; height: 100%; width: 100%; flex-direction: column; align-items: center; justify-content: center;
	.popWrap {     max-height: calc(100% - 40px); display: flex; flex-direction: column; border-radius: 16px; background: #FFFFFF; box-shadow: 0px 8px 32px 0px rgba(60, 65, 116, 0.08);
		&.w1024 { width: 1024px;}
		&.w920 { width: 920px;}
		&.w720 { width: 720px;}

		.pop_head { display: flex; align-items: center; height: 80px; padding: 10px 30px 10px 40px; border-bottom: 1px solid #EEEEEE;
			h1 { flex: 1; font-size: 20px; font-weight: 700;}
			ion-button { width: 36px; height: 36px; --background: transparent; text-indent: -99999rem;
				&.btn_pop_close { background: url("../images/btn_ico_close.svg") center no-repeat;}
			}
		}
		.pop_cont { flex: 1; overflow: auto; padding: 40px 40px 20px;
			.gridwrap { border: none; box-shadow: none; padding: 0; border-radius: 0;
				.grid_option { padding-right: 0;}
			}
			.flex_cont { border-top: 1px solid #333333;}
			.cont_box .row:last-child { margin-bottom: 0;}
		}
		.pop_foot { padding: 20px 40px 40px;
			.footer_btn { margin: 0;
				> div { column-gap: 10px;}
			}
		}
	}
	
}

.hide { display: none !important;}

