.wrapper{ display: flex; height: 100%; overflow: hidden; background-color: #F5F4FC; }
.col_detail { flex: 1;}
.col_list { z-index: 9999999999;}


.btn_pin { width: 28px; height: 28px; --background: transparent; --background-hover: tranparent; text-indent: -99999rem; background: url("../../assets/images/btn_ico_pin.svg") center no-repeat; margin: 15px;}

.collapse { 
	.btn_pin {  display: inline-block;}
}
.btn_noti { position: relative; --background: transparent; --background-hover: transparent !important; width: 24px; height: 24px; padding: 0; text-indent: -9999999rem; background: url("../../assets/images/btn_ico_alarm.svg") center no-repeat; margin: 0 20px;
	&.has_new::before { position: absolute; right: -3.5px; top: -2px; width: 8px; height: 8px; border: 4px solid #F5F4FB; background-color: #FF0000; content: ""; border-radius: 50%; -webkit-border-radius: 50%; -moz-border-radius: 50%; -ms-border-radius: 50%; -o-border-radius: 50%; }
}
.pointer { cursor: pointer; }