.dialog { display: flex; height: 100%; width: 100%; flex-direction: column; align-items: center; justify-content: center;
	.dialogWrap {  min-width: 500px;  max-height: calc(100% - 40px); display: flex; flex-direction: column; border-radius: 16px; background: #FFFFFF; box-shadow: 0px 8px 32px 0px rgba(60, 65, 116, 0.08);
		.dialog_head { display: flex; align-items: center; height: 80px; padding: 10px 30px 10px 40px;
			h1 { flex: 1; font-size: 20px; font-weight: 700;}
		}
		.dialog_cont { flex: 1; overflow: auto; padding: 40px 30px;
			.gridwrap { border: none; box-shadow: none; padding: 0; border-radius: 0;
				.grid_option { padding-right: 0;}
			}
			.flex_cont { border-top: 1px solid #333333;}
			.cont_box .row:last-child { margin-bottom: 0;}
		}
		.dialog_foot { padding: 0 30px 30px;
			.footer_btn { margin: 0;
				> div { column-gap: 10px;}
			}
		}
	}
	&.popup_status {
		.dialogWrap { min-width: 448px; width: 448px; min-height: 342px; border-radius: 8px; background: var(--White, #FFF); box-shadow: 0px 4px 20px 0px rgba(62, 61, 58, 0.30);
			.dialog_head { display: none;}
			.dialog_cont { text-align: center; padding: 40px 20px;
				h1 { color: #44433F; font-size: 28px; font-weight: 500; line-height: 34px; text-align: center; padding-top: 116px; background-position: top center; background-repeat: no-repeat; 
					&.success { background-image: url("../../../assets/images/ico_pop_success.svg");}
					&.error { background-image: url("../../../assets/images/ico_pop_error.svg");}
					&.info { background-image: url("../../../assets/images/ico_pop_info.svg");}
					&.warning { background-image: url("../../../assets/images/ico_pop_warning.svg");}
					&.question { background-image: url("../../../assets/images/ico_pop_question.svg");}
				}
				p { color: #44433F; margin-top: 12px; font-size: 16px; font-weight: 400; line-height: 20px; }
			}
			.dialog_foot { text-align: center; padding: 0 20px 40px;
				.footer_btn { justify-content: center; column-gap: 16px;
					> * { justify-content: center;}
				}
			}
		}
	}
}


.backdrop { position: fixed; top: 0; left: 0; width: 100%; height: 100vh; z-index: 10; background-color: rgba(0, 0, 0, 0.5); }